import axios from 'axios';


export default{
    namespaced:true,
    state:{
        goodsAll:[],
        showData:[],
        cartData:[],
    },
    mutations:{
        setGoodsAll(state,payload){
            state.goodsAll = payload;
        },
        setShowInfo(state,payload){
            state.showData = payload;
        },
        setCartInfo(state,payload){
            state.cartData = payload;
        }
    },
    actions:{
        requestShopInfo(context,payload){
            axios.post('/app/api/goods/goods_info',{

            })
            .then(data=>{
                
                const {goodsAll} =  data.data.data;
                // console.log(goodsAll);
                context.commit('setGoodsAll',goodsAll);
            })
            .catch(err=>{
                console.log(err);
            })
        },
        // 请求单个商品信息
        requestShowInfo(context,payload){
            axios.post('/app/api/goods/goods_id',{
                id:payload,
            })
            .then(data=>{
               
                const {goodsShow} =data.data.data;
                // console.log(goodsShow);
                context.commit('setShowInfo',goodsShow);
            })
            .catch(err=>{
                console.log(err)
            })
        },

        // 添加购物车的商品信息
        requestShopcartAdd(context,payload){
            axios.post('/app/api/shopcart/shopcart_add',{
                id:payload,
            })
            .then(data=>{
                alert('添加成功');
            })
            .catch(err=>{
                console.log(err)
            })
        },
        requestShopcartInfo(context,payload){
            axios.post('/app/api/shopcart/shopcart_info')
            .then(data=>{
                // alert('添加成功');
                
                const {goodsData} = data.data.data;
                // console.log(goodsData);
                context.commit('setCartInfo',goodsData);
            })
            .catch(err=>{
                console.log(err)
            })
        },

        // 购物车详情
        requestFlagInfo(conntext,payload){
            axios.post('/app/api/shopcart/shopcart_flag',{
                item:payload.item,
                type:payload.type

            })
            .then(data=>{
                console.log(data)
            })
            .catch(err=>{
                console.log(err)
            })
        }
    }
}