import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    redirect: 'shop',
    component: () => import(/* webpackChunkName: "about" */ '../views/Home.vue'),
    children: [
      // 商城
      {
        path: 'shop',
        name: 'shop',
        component: () => import('../views/Shop.vue'),
        children: [
          // 商品展示
          {
            path: ':id',
            name: 'goodshow',
            component: () => import('../components/good/GoodShow.vue'),
          }
        ]
      },
      // 分类
      {
        path: 'category',
        name: 'category',
        component: () => import('../views/Category.vue'),
        children: [
          // 商品展示
          {
            path: ':id',
            name: 'goodshow1',
            component: () => import('../components/good/GoodShow.vue'),
          }
        ]
      },
      // 购物车
      {
        path: 'shopcart',
        name: 'shopcart',
        component: () => import('../views/Shopcart.vue'),
      },
      // 我的
      {
        path: 'user',
        name: 'user',
        component: () => import('../views/User.vue'),
      },

    ]
  },


  {
    path: '**',
    // redirect: '/',
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
