
import axios from 'axios';
export default{
    namespaced:true,
    state:{
        cateData:[],

    },
    mutations:{
        setCategoryInfo(state,payload){
            state.cateData = payload;
        }
    },
    actions:{
        requireCategoryInfo(context,payload){
            axios.post('/app/api/goods/goods_category',{
                category:payload,
            })
            .then(data=>{
                
                const {goodsCate} = data.data.data;
                // console.log(goodsCate);
                context.commit('setCategoryInfo',goodsCate);
            })
            .catch(err=>{
                console.log(err);
            })
        }

    }
}