// 全局导入的模块，可以再全局进行使用

import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/iconfont.css'
import Plugin from './plugin'


// console.log(Vue);

Vue.use(Plugin);
Vue.config.productionTip = false



new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
